'use client'

import { ListItem, Typography, type ListItemProps } from '@mui/material';
import { Fragment, JSX, useEffect, useState } from 'react';
import { type ProjectData } from '@strife/common';

export interface ProjectListItemProps extends ListItemProps {
  project: ProjectData
}

export default function ProjectListItem({ project, ...props }: ProjectListItemProps): JSX.Element {
  return <ListItem {...props}>
    <Typography>{project.name}</Typography>
    {
      project.description
        ? <Typography>{project.description}</Typography>
        : undefined
    }
  </ListItem>;
}
