'use client'

import ProjectListItem from '@components/project/ProjectListItem';
import { Box, Divider, List, Typography, ListItem, ListItemText, type ListProps } from '@mui/material';
import { type ProjectData } from '@strife/common';
import { JSX } from 'react';

const defaultEmpty = 
  <ListItem sx={{ justifyContent: 'center', textAlign: 'center' }}>
    <ListItemText>There are no items to display here.</ListItemText>
  </ListItem>;

export interface ProjectListProps extends ListProps {
  label: string,
  projects: ProjectData[],
  action?: React.ReactNode,
  empty?: React.ReactNode
}

export default function ProjectList({ label, action, empty, projects, ...listProps }: ProjectListProps): JSX.Element {
  const projectListItems = projects.map((project: ProjectData, index: number): JSX.Element => <ProjectListItem key={index} project={project} />);
  return <Box>
    <Box display='flex' alignItems='left' justifyContent='space-between' padding={2}>
      <Typography variant='h6'>{label}</Typography>
      {action}
    </Box>
    <Divider />
    <List {...listProps}>
      {
        projectListItems.length > 0
          ? projectListItems
          : <ListItem>{empty ?? defaultEmpty}</ListItem>
      }
    </List>
  </Box>
}